import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';

@IonicPage({
})
@Component({
  selector: 'page-email-list',
  templateUrl: 'email-list.html',
})
export class EmailListPage {

  ///标题名称
  titlename: string = "内部邮箱";

  ///数据的列表
  datalist: Array<any> = [];

  ///数据连接
  dataurl: string = "Email/GetEmailList";

  ///id的列信息
  idcol: string = "CODE";

  ///搜索的内容
  searchQuery: string = "";

  ///
  listType: string = "unread";

  ///数据的参数信息
  dataParam: any = {};

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams
  ) {

  }

  public ngOnInit() {
    this._flush();
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad EmailListPage');
  }

  changePage() {
    this._flush();
  }

  private _flush(): void {
    var stateParam: any = {};
    stateParam["ReadState"] = this._getStateInfo();
    this.dataParam = stateParam;
  }

  /**
   * 获取当前单项列表状态
   *
   * @private
   * @returns {string}
   * @memberof STaskListPage
   */
  private _getStateInfo(): string {
    var result: string = "未读";
    if (this.listType == "unread") {
      result = "未读";
    } else if (this.listType == "read") {
      result = "已读";
    } else {
      result = "";
    }
    return result;
  }

  emailDetail(item) {
    this.navCtrl.push("EmailDetailPage", { item });
  }

}



